import React from 'react';
import './Extracurricular.css';

class Extracurricular extends React.Component {
  render() {
    return (
      <div id="extracurricular" className="box">

        <div className="box-heading">
          <h1>Allow me to introduce myself...</h1>
        </div>

        <div className="box-content">
          <p>Hello, my name is Mitali Sawant. I am a second-year Computer Science Grad Student at the
            University of San Francisco. Robotics enthusiast turned into an amateur Web development  Cloud nerd.
          </p>
        </div>

      </div>
    );
  }
}

export default Extracurricular;
