import React from 'react';

import myPhoto from './my-photo.png';
import './About.css';

class About extends React.Component {
  render() {
    return (
      <div id="about" className="box">

        <div className="row" style={{ justifyContent: "center" }}>
          <h1 style={{color: "black"}}>Allow me to&nbsp;</h1>
          <h1 style={{color: "#3399ff"}}>introduce myself...</h1>
        </div>

        <div className="row">

          <div className="column">
            <img
              src={myPhoto}
              style={{float: "left", height: "400px", width: "400px", marginRight: "20px"}}
              alt="myself"
            />
          </div>

          <div className="column">
            <div>
            <p>Hello, my name is Mitali Sawant. I am a second-year Computer Science Graduate Student at the
              University of San Francisco. Robotics enthusiast turned into an aspiring Web development/Cloud expert.
            </p>
            </div>
            <div>
            <button className="resume-button">
              <a href="https://drive.google.com/file/d/1myo-zE5vkYLlXR8bEVUSwVXnojIlOkiH/view?usp=sharing" target="_blank">RESUME</a>
            </button>
            </div>
          </div>

        </div>

        <div className="row" style={{ justifyContent: "flex-start" }}>
          <h4 style={{color: "#3399ff"}}>Technical&nbsp;</h4>
          <h4 style={{color: "#3399ff"}}>Skills</h4>
        </div>

        <div className="row" style={{ justifyContent: "space-between" }}>
          <div className="column" style={{ alignItems: "flex-start" }}>
            <h5>Languages</h5>
            <ul>
              <li>Java</li>
              <li>C</li>
              <li>Python</li>
              <li>JavaScript</li>
            </ul>
          </div>
          <div className="column" style={{ alignItems: "flex-start" }}>
            <h5>Web Technologies</h5>
            <ul>
              <li>JWT</li>
              <li>Ajax</li>
              <li>REST APIs</li>
              <li>Docker</li>
            </ul>
          </div>
          <div className="column" style={{ alignItems: "flex-start" }}>
            <h5>Databases</h5>
            <ul>
              <li>MySQL</li>
              <li>MongoDB</li>
              <li>H2</li>
              <li>SQLite</li>
            </ul>
          </div>
          <div className="column" style={{ alignItems: "flex-start" }}>
            <h5>Tools</h5>
            <ul>
              <li>Git</li>
              <li>Maven</li>
              <li>IntelliJ</li>
              <li>PyCharm</li>
            </ul>
          </div>
          <div className="column" style={{ alignItems: "flex-start" }}>
            <h5>Big Data Technologies</h5>
            <ul>
              <li>MapReduce</li>
              <li>Apache Beam</li>
              <li>BigQuery</li>
              <li>Google Dataflow</li>
            </ul>
          </div>
        </div>

      </div>
    );
  }
}

export default About;
